import musichead from '../../components/musichead/musichead.vue'
import '@/common/iconfont.css'
import { searchHot, searchWord, searchSuggest } from '../../common/api.js'

export default {
	data() {
		return {
			searchHotData:[],
			searchWord: '',
			searchHistory: [],
			searchType: 1,
			searchList: [],
			searchSuggest: [],
		}
	},
	components:{
		musichead
	},
	onLoad(){
		searchHot().then((res)=>{
			// console.log(res)
			if(res[1].data.code == '200'){
			this.searchHotData = res[1].data.data;
			// console.log(this.searchHotData)
			}
		})
		uni.getStorage({
			key: 'searchHistory',
			success: (res)=>{
				this.searchHistory = res.data;
			}
		})
	},
	onShow(){
		
	},
	methods: {
		handleToWord(word){
			this.searchWord = word;
			this.handleToSearch(word);
		},
		handleToSearch(word){
			// 放入存储历史搜索的数组前面
			this.searchHistory.unshift(word);
			// 去重
			this.searchHistory = [...new Set(this.searchHistory)]
			// 保持最多十个搜索记录
			if(this.searchHistory.length>10){
				this.searchHistory.length = 10;
			}
			// 存储搜索数据至本地存储local storage 避免刷新就没了
			// 刷新时 在onload里调用getstorage方法
			uni.setStorage({
				key: 'searchHistory',
				data: this.searchHistory
			});
			this.getSearchList(word);
		},
		handleToClear(){
			uni.removeStorage({
				key:'searchHistory',
				success: (res)=>{
					this.searchHistory = [];
				}
			})
		},
		getSearchList(word){
			searchWord(word).then((res)=>{
				if(res[1].data.code == '200'){
					console.log(res, word)
					this.searchList = res[1].data.result.songs;
					console.log('searchlist', this.searchlist)
					this.searchType = 2;
				}
			})
		},
		handleToClose(){
			this.searchWord = ''
			this.searchType = 1;
			
		},
		handleToDetail(songId){
			uni.navigateTo({
				url: '/pages/detail/detail?songId='+songId
			});
		},
		handleToSuggest(ev){
			// input中 想拿到输入的值 就从event.detail.value中拿
			let value = ev.detail.value;
			if(!value){
				this.searchType = 1;
				return;
			}
			console.log('value',value)
			searchSuggest(value).then((res)=>{
				if((res[1]).data.code == '200'){
					console.log(res)
					this.searchSuggest = res[1].data.result.allMatch;
					console.log(this.searchSuggest)
					this.searchType = 3;
				}
			})
		}
	}
}
